.tabItems{
    box-sizing: border-box;
    display: flex;
    justify-content: center;
}

.tabItems ol{
    padding-left: 0;
}

.tabItems li {
    position: relative;
    display: inline-block;
    float: none;
    width:auto;
    margin-left: 30px;
    margin-right: 30px;
    padding: 4px 15px 4px 15px;
    list-style-type: none;
    cursor: pointer;
}

.tabItems li.active {
    background-color: #929e9e;
    color: #fff;
    border-radius: 15px;
}